:root {
  --safe-area-top: env(safe-area-inset-top);
  --safe-area-right: env(safe-area-inset-right);
  --safe-area-bottom: env(safe-area-inset-bottom);
  --safe-area-left: env(safe-area-inset-left);
}

* {
  padding: 0;
  margin: 0;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

html,
body {
  overscroll-behavior: none;
  /* background-color: #000; */
}

a,
a:visited,
a:active {
  color: white;
}

a:hover {
  color: #487ef7;
}

.instructions {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  color: white;
  padding: 40px;
}

.content {
  margin: 0 auto;
  max-width: 500px;
}

.content--win {
  display: none;
}

.click-to-play {
  margin-top: 10px;
  font-size: 20px;
  margin-bottom: 20px;
  opacity: 0.5;
}

.commands {
  margin-bottom: 20px;
}

.inspired-by {
  font-size: 14px;
  opacity: 0.5;
}
